  background-color: #fff;
}
:host ion-list {
  background-color: white;
}

ion-toolbar {
  height: 60px !important;
}

.dashboard-button {
  display: flex;
  column-gap: 30px;
  flex-wrap: wrap;
}
.dashboard-button .s-button {
  width: 85px;
  margin-bottom: 30px;
  box-shadow: 0px 0px 3px 1px #F27494;
  border-radius: 15px;
}
.dashboard-button .s-button .dashboard-icons {
  width: 100%;
  height: 65px;
  width: 65px;
  border-radius: 65px;
  border: 1px solid rgba(192, 186, 230, 0.5294117647);
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(192, 186, 230, 0.5294117647);
}
.dashboard-button .s-button .dashboard-icons img {
  width: 55%;
}
.dashboard-button .s-button ion-button {
  width: 100%;
}
.dashboard-button .s-button.color1 .dashboard-icons {
  background: #bcea95;
  border: 1px solid #bcea95;
}
.dashboard-button .s-button.color2 .dashboard-icons {
  background: #aff5ff;
  border: 1px solid #aff5ff;
}
.dashboard-button .s-button.color3 .dashboard-icons {
  background: #fdcbe2;
  border: 1px solid #fdcbe2;
}
.dashboard-button .s-button.color4 .dashboard-icons {
  background: #ffde87;
  border: 1px solid #ffde87;
}

.dashboard-text {
  color: #000 !important;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.ion-padding {
  padding: 30px 20px;
}

.name-padding {
  margin-top: 30px;
}

.height-name {
  display: flex;
  flex-wrap: wrap;
  font-size: 13px;
  font-weight: 500;
  padding: 5px 10px;
  color: #623AA2 !important;
  background: #F3EBFF;
  border: 1px solid #623AA2;
  margin: 15px 15px 5px;
  border-radius: 4px;
  align-items: center;
}
.height-name .teacher-image {
  width: 35px;
  height: 35px;
  margin-left: 0px;
  border-radius: 65px;
  overflow: hidden;
  margin-right: 20px;
}
.height-name .teacher-name {
  font-size: 14px;
  line-height: 1.3;
  font-weight: 400;
}
.height-name .subject {
  font-size: 12px;
  line-height: 1;
  font-weight: 400;
}
.height-name .left {
  padding-left: 10px;
}

.card-height {
  height: 100%;
  margin: 0;
  border: 0;
  box-shadow: none;
  border-radius: 0px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

ion-title {
  --color:#fff ;
}

ion-button .ion-color {
  --color:red !important ;
}

.class-name {
  font-size: 20px;
  font-weight: 600;
  padding-left: 20px !important;
}

.roll-number {
  font-size: 12px;
}

.w-100 {
  width: 100%;
  display: block;
}

ion-avatar {
  width: 32px;
  height: 32px;
  border: 1px solid #ccc;
}

.school-name {
  font-size: 12px;
  font-weight: normal;
  padding: 0;
  text-align: left;
}

ion-item-sliding {
  margin-bottom: 10px;
  border: 1px solid #b5b2b2;
  box-shadow: 1px 1px 3px 1px #b5b2b2;
  border-radius: 10px;
}
ion-item-sliding ion-item {
  --background: #fff;
}
ion-item-sliding ion-item ion-label {
  font-size: 14px;
  font-weight: 500;
}

ion-toast.custom-toast {
  --background: #f4f4fa;
  --box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.2);
  --color: #4b4a50;
  top: -60px;
}

ion-toast.custom-toast::part(message) {
  font-style: italic;
}

ion-toast.custom-toast::part(button) {
  border-left: 1px solid #d2d2d2;
  color: #030207;
  font-size: 15px;
}

ion-toast.custom-toast2 {
  --background: #833333;
  --box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.2);
  --color: #4b4a50;
  top: -70px;
}

ion-toast.custom-toast2::part(message) {
  font-style: italic;
}

ion-toast.custom-toas2t::part(button) {
  border-left: 1px solid #d2d2d2;
  color: #030207;
  font-size: 15px;
